@import "https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400;500;700;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #f7ffd6;
  background-color: #a8aba4;
  font-family: Maven Pro, sans-serif;
  font-weight: 400;
}

.container-main-hidden {
  width: 100vw;
  min-height: 100vh;
  height: auto;
  background-color: #a8aba46e;
  position: relative;
  top: 100vh;
}

.flip-container {
  width: 100vw;
  grid-template-rows: repeat(16, 1fr);
  grid-template-columns: repeat(22, 1fr);
  display: grid;
}

.container-one {
  width: 100%;
  height: 100%;
  z-index: -2;
  grid-template-rows: repeat(16, 1fr);
  grid-template-columns: repeat(20, 1fr);
  display: grid;
  position: fixed;
  top: 0;
}

.line1 {
  border-left: 2px solid #f7ffd6;
  grid-area: 1 / 5 / 5 / 6;
}

.line2 {
  writing-mode: vertical-rl;
  text-orientation: sideways;
  width: -moz-fit-content;
  width: fit-content;
  text-align: center;
  border-left: 2px solid #f7ffd6;
  grid-area: 12 / 5 / 22 / 6;
}

.card-image {
  width: auto;
  min-width: 150px;
  background-color: #a8aba4;
  border: 2px solid #f7ffd6;
  flex-direction: column;
  grid-area: 5 / 3 / 12 / 7;
  justify-content: center;
  align-items: center;
  margin: 10px;
  padding: 10px;
  transition: all .5s ease-in-out;
  display: flex;
  box-shadow: 6px 6px 10px #5c5b5b40, -6px -6px 10px #ffffff4d;
}

.my-img {
  width: 100%;
  height: 90%;
  background-image: url("my-img2.f714e0bb.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.name {
  color: #f7ffd6;
  text-align: center;
  text-transform: uppercase;
  grid-area: 6 / 3 / 7 / 6;
  margin-top: 10px;
  font-size: 1.4rem;
}

.title {
  color: #f7ffd6;
  letter-spacing: 1rem;
  opacity: 1;
  grid-area: 5 / 8 / 7 / 21;
  font-size: 6rem;
  transition: all 1s ease-out;
  transform: translateX(0%);
}

.container-main-hidden:hover .title {
  opacity: 0;
  transform: translateX(100%);
}

.profession {
  color: #f7ffd6;
  opacity: 1;
  border-top: 2px solid #f7ffd6;
  grid-area: 7 / 8 / 8 / 21;
  font-size: 3.6rem;
  transition: all 1s ease-out;
  transform: translateX(0%);
}

.container-main-hidden:hover .profession {
  opacity: 0;
  transform: translateX(100%);
}

.projects {
  color: #f7ffd6;
  opacity: 0;
  border-top: 2px solid #f7ffd6;
  grid-area: 7 / 8 / 8 / 21;
  font-size: 3.6rem;
  transition: all .6s ease-out;
  transform: translateY(300%);
}

.container-main-hidden:hover .projects {
  opacity: 1;
  transform: translateY(0%);
}

.text {
  text-align: center;
  color: #f7ffd6;
  font-size: 2rem;
  line-height: 150px;
}

.item {
  width: 100%;
  background-color: #f7ffd6;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: all .5s ease-in-out;
  box-shadow: 6px 6px 10px #5c5b5b40, -6px -6px 10px #ffffff4d;
}

.item:hover {
  color: #ff009d;
  color: #333131;
  background-color: #f9fced;
}

.description {
  text-align: center;
  padding: 10px;
  display: none;
}

.item1:hover .description, .item2:hover .description, .item3:hover .description {
  display: block;
}

.item1:hover .text, .item2:hover .text, .item3:hover .text {
  display: none;
}

.item1 {
  background-image: url("item-two.f8e5a014.jpeg");
  grid-area: 1 / 6 / 4 / 10;
}

.item1:hover, .item2:hover, .item3:hover {
  background-image: none;
}

.item2 {
  background-image: url("item-three.061d238a.jpeg");
  grid-area: 3 / 16 / 6 / 22;
}

.item3 {
  background-image: url("item-four.434f04dc.jpeg");
  grid-area: 13 / 16 / 17 / 22;
}

.item {
  width: 100%;
  background-color: #f7ffd6;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: all .5s ease-in-out;
  box-shadow: 6px 6px 10px #5c5b5b40, -6px -6px 10px #ffffff4d;
}

.item4 {
