}

.item5 {
  grid-area: 12 / 13 / 15 / 16;
}

.item6 {
  grid-area: 8 / 14 / 11 / 17;
}

.item7 {
  grid-area: 13 / 10 / 16 / 12;
}

.item8 {
  grid-area: 9 / 18 / 12 / 21;
}

#form {
  width: auto;
  height: auto;
  border: 1px solid red;
  display: none;
}

#form:hover {
  display: block;
}

form {
  width: 90%;
  margin: 10px auto;
  display: block;
}

input {
  width: 100%;
  display: inline-block;
}

textarea {
  width: 60%;
  display: inline-block;
}

input:focus, textarea:focus {
  border: 0;
  outline: 2px solid #00f;
}

input[type="submit"] {
  width: 65px;
  margin: 8px auto;
  display: block;
}

.flip-card {
  perspective: 1000px;
  background-color: #0000;
}

.flip-card-inner {
  width: 100%;
  height: 100%;
  text-align: center;
  transform-style: preserve-3d;
  transition: transform .8s;
  position: relative;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  position: absolute;
}

.flip-card-front {
  color: #000;
  background-color: #a8aba4;
}

.flip-card-back {
  color: #333131;
  background-color: #f7ffd6;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  transform: rotateY(180deg);
}

.center {
  color: #f7ffd6;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

a {
  color: #333131;
  text-decoration: none;
}

a:hover {
  text-decoration: underline #333131;
  text-decoration-thickness: 3px;
}

@media screen and (max-width: 768px) {
  .container-one {
    z-index: -2;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: flex;
    position: fixed;
    top: 0;
  }

  .line2 {
    text-align: center;
    writing-mode: horizontal-tb;
    width: auto;
    line-height: auto;
    order: 3;
  }

  .title {
    width: auto;
    text-align: center;
    padding: 20px;
    font-size: 2.6rem;
  }

  .profession {
    text-align: center;
    font-size: 2rem;
  }

  .item1, .item2, .item3 {
    width: 300px;
    max-width: 400px;
    opacity: 1;
    border: 2px solid #f7ffd6;
    margin: 10px;
    transition: all 1s ease-out;
    transform: translateX(0%);
  }

  .container-main-hidden:hover .item1 {
    opacity: 0;
    transform: translateX(-100%);
  }

  .container-main-hidden:hover .item2 {
    opacity: 0;
    transform: translateX(100%);
  }

  .container-main-hidden:hover .item3 {
    opacity: 0;
    transform: translateX(-100%);
  }

  .container-main-hidden {
    flex-direction: column;
    display: flex;
  }

  .flip-container {
    height: 80vh;
    background-color: #a8aba44f;
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .flip-container * {
    width: 300px !important;
    height: 150px !important;
  }

  .flip-card {
    margin: 10px;
  }
}

